@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#eb8989;color:#523333}body button{color:#523333}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.app .app__header{display:flex;align-items:center;justify-content:center;width:100%;border-bottom:6px solid #db7f7f;margin-bottom:5rem}.app .app__header .app__logo{display:flex;justify-content:center;align-items:center;margin:0 5rem}@media screen and (max-width: 992px){.app .app__header .app__logo{margin:0 1rem}}.app .app__header .app__logo--img{height:2rem;margin-right:1rem}.app .app__body{margin:0 10%}.app__anchors{color:#523333;text-decoration:none !important}

.articleList{padding:0.5rem}.list{display:flex;flex-wrap:wrap;justify-content:space-between}.list__card{display:flex;justify-content:center;flex-direction:row;align-items:center;width:25%;border-radius:5px;padding:1rem 1rem;background:#eb8989;box-shadow:-7px -7px 0px #db7f7f, 7px 7px 0px #fb9393;position:relative;margin-bottom:3rem;transition:ease-out 0.5s}@media screen and (max-width: 992px){.list__card{width:100%}}.list__card:hover{background:#eb8989;box-shadow:-6px -6px 7px #db7f7f, 6px 6px 7px #fb9393;transition:0.5s}.list__card a{text-decoration:none;color:#523333}

.btn button{cursor:pointer;outline:none;border:none;border-radius:5px;background:#eb8989;box-shadow:-6px -6px 7px #db7f7f, 6px 6px 7px #fb9393;padding:1rem;font-weight:700;transition:0.5s ease-out;min-width:10rem}@media screen and (max-width: 992px){.btn button{margin:1rem 0}}.btn button:hover{background:#eb8989;box-shadow:-7px -7px 0px #db7f7f, 7px 7px 0px #fb9393;transition:0.5s}.btn button p{margin:0}

.single{width:60%;margin:0 auto}@media screen and (max-width: 992px){.single{width:95%}}.single__post,.single__comments{display:flex;justify-content:center;flex-direction:column;align-items:center;border-radius:5px;padding:1.5rem 1rem;background:#eb8989;box-shadow:-7px -7px 0px #db7f7f, 7px 7px 0px #fb9393;position:relative;margin-bottom:3rem}.single__post p,.single__comments p{width:90%;margin:0 auto}.single__post h4,.single__comments h4{margin-bottom:1rem;margin-top:0;width:100%}.single__btn{display:flex;justify-content:space-between;flex-wrap:wrap}@media screen and (max-width: 992px){.single__btn{justify-content:center;margin:1rem 0}}.single__comments{margin-top:3rem}.single__comments--header{display:flex;align-items:flex-start;width:100%}.single__comments--header h5{margin-top:0}.single__comments--header .header-flex{display:flex;flex-direction:column;align-items:flex-start;justify-content:flex-start}.single__comments--header .header-flex.name{width:60%}.single__comments--header .header-flex p{margin:0 0 5px 0}

